import { useContext } from "react";
import { useAuth } from "../../context/AuthContext";

import { ProfileContext } from "../../backend/ProfileProvider";
import ProfileDomainCard from "../base/ProfileDomainCard";

const Profile = () => {
	const user = useAuth();
	let state = useContext(ProfileContext);

	if (state.loading) {
		return <div className="spinner"></div>;
	}

	return (
		<div className="profile-page">
			<div style={{ marginBottom: "2rem" }}>
				<h1 className="detail">
					{user.first_name} {user.last_name}
				</h1>

				<h2 className="detail">{user.email}</h2>
				<h2 className="detail">{user.registration_number}</h2>
			</div>
			<h1 style={{ fontSize: "2.6rem" }}>Your Domains</h1>
			<div className="domains">
				{state.count.map((domain, idx) => (
					<ProfileDomainCard
						icon={domain.icon}
						title={domain.title}
						key={idx}
						slug={domain.slug}
						short_description={domain.short_description}
						count_assigned={domain.count_assigned}
						count_completed={domain.count_completed}
						startdate={domain.startdate}
					/>
				))}
			</div>
		</div>
	);
};

export default Profile;
