import React, { useState } from "react";
import isteLogo from "../../assets/iste.svg";
import DomainImg from "../../assets/d.png";
import ProfileImg from "../../assets/p.png";
import LogoutImg from "../../assets/s.png";

import { Link } from "react-router-dom";

const Navbar = () => {
	const [clicked, setClicked] = useState(0);

	const navSlide = () => {
		setClicked(!clicked);

		const burger = document.querySelector(".burger");
		const nav = document.querySelector(".nav-links");
		const navLinks = document.querySelectorAll(".nav-links li");
		if (!clicked) {
			nav.classList.add("nav-active");
			navLinks.forEach((link, index) => {
				if (link.style.animation) {
					link.style.animation = "";
				} else {
					link.style.animation = `navLinkFade 0.5s ease forwards ${
						index / 7 + 0.3
					}s`;
				}
			});
			burger.classList.add("toggle");
		} else {
			nav.classList.remove("nav-active");
			navLinks.forEach((link, index) => {
				if (link.style.animation) {
					link.style.animation = "";
				} else {
					link.style.animation = `navLinkFade 0.5s ease forwards ${
						index / 7 + 0.3
					}s`;
				}
			});
			burger.classList.remove("toggle");
		}
	};

	return (
		<nav>
			<div className="logo">
				<Link to="/">
					<img src={isteLogo} alt="iste-logo" className="iste-logo" />
				</Link>
				<h4 className="nav-title"> ISTE Summer School</h4>
			</div>
			<ul className="nav-links">
				<li>
					<img src={DomainImg} className="nav-imgs" alt="domain-img" />
					<Link to="/">Domains</Link>
				</li>
				<li>
					<img src={ProfileImg} className="nav-imgs" alt="profile-img" />
					<Link to="/profile">Profile</Link>
				</li>
				<li>
					<img src={LogoutImg} className="nav-imgs" alt="logout-img" />
					<a href="/logout">Logout</a>
				</li>
			</ul>
			<div
				className="burger"
				onClick={() => {
					navSlide();
				}}
			>
				<div className="line1"></div>
				<div className="line2"></div>
				<div className="line3"></div>
			</div>
		</nav>
	);
};

export default Navbar;
