import { useContext } from "react";
import DomainCard from "../base/DomainCard";

import LoaderCard from "./LoaderCard";

import { HomeContext } from "../../backend/HomeProvider";

export default function Home() {
	const state = useContext(HomeContext);
	if (state.loading) {
		return <LoaderCard />;
	}
	return (
		<div className="home">
			<div className="cards">
				{state.count.map((domain, idx) => {
					return <DomainCard {...domain} key={idx} />;
				})}
			</div>
		</div>
	);
}
