import React from "react";
import { Switch, Route } from "react-router-dom";

import Home from "./components/pages/Home";
import Auth from "./components/pages/Auth";
import Tasks from "./components/pages/Tasks";
import Profile from "./components/pages/Profile";

import Navbar from "./components/utils/Navbar";

import { HomeProvider } from "./backend/HomeProvider";
import { ProfileProvider } from "./backend/ProfileProvider";
import TaskProvider from "./backend/TaskProvider";
import NotFound from "./components/utils/NotFound";

function App() {
	return (
		<React.Fragment>
			<Navbar />
			<Switch>
				<Route
					exact
					path="/"
					render={() => (
						<HomeProvider>
							<Home />
						</HomeProvider>
					)}
				/>
				<Route
					path="/tasks/:domainID/:taskID"
					render={() => (
						<TaskProvider>
							<Tasks />
						</TaskProvider>
					)}
				/>
				<Route path="/auth" component={Auth} />
				<Route
					path="/profile"
					render={() => (
						<ProfileProvider>
							<Profile />
						</ProfileProvider>
					)}
				/>
				<Route
					path="/tasks/:domainID"
					render={() => (
						<TaskProvider>
							<Tasks />
						</TaskProvider>
					)}
				/>
				<Route exact path="/not-found" component={NotFound} />
			</Switch>
		</React.Fragment>
	);
}

export default App;
