import Swal from "sweetalert2";

export const showSuccessfulAlert = (message: string) => {
	Swal.fire({
		title: "Success",
		text: message,
		toast: true,
		icon: "success",
		position: "top",
	});
};

export const showUnsuccessfulAlert = (message: string) => {
	Swal.fire({
		title: "Error",
		text: message,
		icon: "error",
		position: "top",
	});
};

export const showYesNoAlert = async (
	message: string,
	onYesFunction: () => void,
	onNoFunction?: () => void
) => {
	const value = await Swal.fire({
		title: "Confirmation",
		text: message,
		icon: "warning",
		showConfirmButton: true,
		showCancelButton: true,
	});

	if (value.isConfirmed) {
		onYesFunction();
	} else {
		if (onNoFunction) {
			onNoFunction();
		}
	}
};
