import React from "react";
import ReactTooltip from "react-tooltip";
import moment from "moment";

import parse from "html-react-parser";

import { useHistory } from "react-router-dom";
export default function TaskCard(props) {
	const history = useHistory();

	const render_tooltip = () => {
		if (props.submission === null) {
			return <></>;
		}
		if (props.submission.comments !== null) {
			//not reviewed
			return (
				<>
					<div data-tip="Completed!" className="completed">
						<i className="fas fa-check-circle"></i>
					</div>
					<ReactTooltip effect="solid" type="success" />
				</>
			);
		} else {
			return (
				<>
					<div data-tip="Submitted" className="submitted">
						<i className="fas fa-check-circle"></i>
					</div>
					<ReactTooltip effect="solid" type="warning" />
				</>
			);
		}
	};

	return (
		<div className="task-card">
			{render_tooltip()}
			<div>
				<h1>{props.title}</h1>
				<div className="date">
					<p>
						Start Date :{" "}
						<span style={{ fontWeight: "700" }}>
							{moment(props.start_date).format(" Do MMMM, dddd")}
						</span>
					</p>
					<p>
						End Date:
						<span style={{ fontWeight: "700" }}>
							{props.last_date === null
								? "NA"
								: moment(props.last_date).format(" Do MMMM, dddd")}
						</span>
					</p>
				</div>
			</div>
			<div className="bottom">
				<p>{parse(props.description)}</p>
			</div>
			{/* href={`/tasks/${props.domain}/${props.uuid}`} */}
			<button
				onClick={() =>
					history.push({
						pathname: `/tasks/${props.domain}/${props.uuid}`,
						state: props,
					})
				}
			>
				View full task
			</button>
		</div>
	);
}
