import React, { Component, createContext } from "react";
import { CategoryType } from "./types";
import axios from "../utils/axiosurl";
import { showUnsuccessfulAlert } from "../services/alertService";

type StateType = {
	loading: boolean;
	count: Array<CategoryType>;
};

export const HomeContext = createContext<StateType>({
	count: [],
	loading: true,
});

export class HomeProvider extends Component<{}, StateType> {
	constructor(props: any) {
		super(props);
		this.state = {
			loading: true,
			count: [],
		};
	}

	componentDidMount() {
		type DataType = {
			status: "successful" | "unsuccessful";
			count: CategoryType[];
		};
		axios
			.get("/course/get_categories")
			.then(({ data }: { data: DataType }) => {
				if (data.status === "successful") {
					//console.log(data.count);
					this.setState({
						loading: false,
						count: data.count,
					});
				}
			})
			.catch(err => showUnsuccessfulAlert(err));
	}

	render() {
		return (
			<HomeContext.Provider value={this.state}>
				{this.props.children}
			</HomeContext.Provider>
		);
	}
}

export const HomeConsumer = HomeContext.Consumer;
