import pdfImage from "../../assets/pdf.png";
import githubImage from "../../assets/github.png";
const ShowSubmissionButton = ({ is_github, github_link, file }) => {
	return (
		<>
			<h1 style={{ margin: "1rem 0", marginBottom: "0" }}>Your Submission</h1>
			<div className="start-flex-col">
				<a
					style={{ backgroundColor: is_github ? "white" : "" }}
					href={is_github ? github_link : file}
					className="bg-contrast pdf-container"
					download={!is_github}
					target={is_github ? "_blank" : ""}
					rel={"noreferrer"}
				>
					<img src={is_github ? githubImage : pdfImage} alt="pdf_file" />{" "}
					<span style={{ color: is_github ? "black" : "" }}>
						View Submission
					</span>
				</a>
			</div>
		</>
	);
};

export default ShowSubmissionButton;
