import { useContext } from "react";
import TaskCard from "../base/TaskCard";
import { useParams } from "react-router-dom";
import LoaderCard from "./LoaderCard";
import TaskSingle from "./TaskSingle";
import { TaskContext } from "../../backend/TaskProvider";

import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";

const MySwal = withReactContent(Swal);

export default function Tasks() {
	const { domainID, taskID } = useParams();
	const showOrganisers = () => {
		let mapped_organisers = state.organisers.map(organiser => (
			<tr>
				<td>{organiser.name}</td>
				<td>{organiser.contact}</td>
			</tr>
		));
		MySwal.fire({
			showConfirmButton: true,
			title: "Organisers",
			html: (
				<table>
					{" "}
					<tr>
						<th>Name</th>
						<th>Contact</th>
					</tr>{" "}
					{mapped_organisers}
				</table>
			),
		});
	};
	let state = useContext(TaskContext);

	let tasksCompleted = false;
	let completed_count = 0;
	if (state) {
		state.tasks.forEach(item => {
			if (item.submission && item.submission.comments) {
				completed_count++;
			}
		});
	}

	if (completed_count === state.tasks.length) {
		tasksCompleted = true;
	}

	if (state.loading) {
		return <LoaderCard />;
	}

	return (
		<div className="tasks">
			<h1 style={{ textAlign: "center" }}>{state.category_title}</h1>
			<div className="organizer-container">
				<button className="organizer" onClick={showOrganisers}>
					View Organisers
				</button>
			</div>
			{taskID && domainID ? (
				<>
					{state.tasks.map((item, idx) => {
						if (item.uuid === taskID) return <TaskSingle key={idx} {...item} />;
						return null;
					})}
				</>
			) : (
				<div className="cards">
					{tasksCompleted ? (
						<div className="completed-alert">
							<h2>
								Congratulations! You have completed all {state.category_title}{" "}
								Tasks ðŸŽ‰{" "}
							</h2>
							<p>Click below to download your certificate</p>
							<a href={`/api/course/get_certificate/${domainID}`}>
								<button>Download e-Certificate</button>
							</a>
						</div>
					) : (
						<></>
					)}

					{state.tasks.map((task, idx) => {
						return <TaskCard domain={domainID} {...task} key={idx} />;
					})}
				</div>
			)}
		</div>
	);
}
