import React, { Component, createContext } from "react";
import { ProfileType } from "./types";
import axios from "../utils/axiosurl";
import { showUnsuccessfulAlert } from "../services/alertService";

type StateType = {
	loading: boolean;
	count: Array<ProfileType>;
};

export const ProfileContext = createContext<StateType>({
	loading: true,
	count: [],
});

export class ProfileProvider extends Component<{}, StateType> {
	constructor(props: any) {
		super(props);
		this.state = {
			loading: true,
			count: [],
		};
	}

	componentDidMount() {
		type DataType = {
			status: "successful" | "unsuccessful";
			count: Array<ProfileType>;
		};

		axios
			.get("/course/profile")
			.then(({ data }: { data: DataType }) => {
				if (data.status === "successful") {
					this.setState({
						loading: false,
						count: data.count,
					});
				}
			})
			.catch(err => showUnsuccessfulAlert(err));
	}

	render() {
		return (
			<ProfileContext.Provider value={this.state}>
				{this.props.children}
			</ProfileContext.Provider>
		);
	}
}

export const ProfileConsumer = ProfileContext.Consumer;
